import React, { useState } from "react";
import "./Action1.css";

function Action1() {
  const questions = [
    "스마트폰이 없으면 손이 떨리고 불안하다.",
    "스마트폰 이용시간을 조절하는 것이 어렵다.",
    "스마트폰에 설치한 앱이 13개 이상이고 이용하는 앱이 대부분이다.",
    "스마트폰으로 인해 업무 수행에 어려움이 있다.",
    "스마트폰 이용으로 인해 건강에 문제가 생긴 적이 있다.",
    "장소를 옮길 때 항상 스마트폰을 가지고 이동한다.",
    "스마트폰 이용으로 인해 인간관계에 문제가 생기 적이 있다.",
    "다른 일을 하는 도중에도 스마트폰 알림이 울리면 바로 확인한다.",
    "스마트폰 사용으로 인해 수면시간에 방해를 받은 적이 있다.",
    "완충된 배터리가 하루동안 지속되기 어렵다.",
  ];

  const [counts, setCounts] = useState(new Array(10).fill(0));

  const handleButtonClick = (index) => {
    const newCounts = [...counts];
    newCounts[index]++;
    setCounts(newCounts);
  };

  const totalClicks = counts.reduce((acc, curr) => acc + curr, 0);

  return (
    <div>
      <div className="survey-container" style={{ backgroundColor: "#ADD8E6" }}>
        <h2>스마트폰 사용에 관한 설문조사</h2>
        <p>맞으면 버튼을 눌러주시고 아니면 다음칸으로 넘어가주세요</p>
        {questions.map((question, index) => (
          <div key={index} className="question-container">
            <p className="question-text">{`${index + 1}. ${question}`}</p>
            <button
              className="click-button"
              onClick={() => handleButtonClick(index)}
            ></button>
          </div>
        ))}
        <hr />
        
      </div>

      <div className="survey-container" style={{ backgroundColor: "#ADD8E6" }}>
      <p className="total-count">당신의 점수는 {totalClicks} : 점입니다</p>
      <p className="total-count" >점수가 10점 이상이면 스마트폰 중독입니다</p>
      </div>
    </div>
  );
}

export default Action1;
